import React, { useEffect } from "react";

export default function ThankYou({ state }) {
  useEffect(() => {
    try {
      if (window.gtag) {
        window.gtag("event", "conversion", {
          send_to: "AW-668502602/QgIiCNu_g9ABEMqU4r4C",
        });
      }
    } catch (e) {}
  });

  return (
    <h2 className="text-center pt-4">
      Thank you for scheduling your appointment we look forward to seeing you on{" "}
      {state.scheduleDate} at {state.scheduleTime} at our 11121 Hickman Road,
      Urbandale, IA 50322 Location!
    </h2>
  );
}
