import React, { useEffect, useState } from "react";
import "react-credit-cards/es/styles-compiled.css";
import Cards from "react-credit-cards";
import Step from "./Step";
import { Redirect } from "react-router-dom";
import TermsOfService from "../TermsOfService";
import axios from "axios";
import {
  MDBInput,
  MDBCol,
  MDBRow,
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBCardHeader,
  MDBModalFooter,
  MDBContainer,
} from "mdbreact";
import BackBtn from "../../assets/back-btn.png";

export default function PaymentForm(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [touchcard, settouchcard] = useState(false);
  const [touchcvv, settouchcvv] = useState(false);
  const [touchname, settouchname] = useState(false);
  const [count, setcount] = useState(0);
  const [touchexp, settouchexp] = useState(false);
  const [failcount, setfail] = useState(0);
  const [modal, setmodal] = useState(false);
  const [payment, setpayment] = useState("");

  const formRef = new React.createRef();

  const cardnumberValidate = (num) => {
    setTouched(true);
    settouchcard(true);
    setcount(count + 1);
    var rgx = new RegExp("^[0-9]+$");
    const isvalid = num.length > 12 && num.length < 20 && rgx.test(num);
    setErrorMsg(isvalid ? "" : "Please enter valid Credit card number ");
  };

  const cvvvalidate = (num) => {
    setTouched(true);
    settouchcvv(true);
    var rgx = new RegExp("^[0-9]+$");
    const isvalid = state.cvv.length > 1 && rgx.test(num);
    console.log(num.length);
    console.log(num);
    setcount(count + 1);
    console.log(isvalid);
    setErrorMsg(isvalid ? "" : "Please enter valid CVV ");
  };

  const validate = () => {
    setTouched(true);
    const isValid =
      formRef.current.checkValidity() &&
      touchcard &&
      touchcvv &&
      touchexp &&
      touchname;

    setErrorMsg(isValid ? "" : "Please enter valid details");
  };

  const [showModal, setShowModal] = useState(false);

  const toggleModal = () => {
    setShowModal(!showModal);
  };

  const toggle = () => {
    setmodal(!modal);
  };

  const submitHandler = () => {
    var config = {
      headers: {
        "Content-Type": "application/json",
        "Accept-Encoding": "gzip, deflate, br",
        "Access-Control-Allow-Origin": "*",
      },
    };

    //api call
    const data = {
      amount: "80",
      card_number: state.creditCardNumber,
      cvv: state.cvv,
      name: state.nameOnCard,
      zipcode: state.paymentzipcode,
      month: state.exp.substring(0, 2),
      year: `20${state.exp.substring(3, 5)}`,
    };
    axios
      .post(
        "https://cors-anywhere.herokuapp.com/https://passportdocpros.com/api/CreatePayment",
        JSON.stringify(data),
        config
      )
      .then((response) => {
        history.push(nextStep);

        setpayment(response.data.message);

        console.log(response.data.message);
      })
      .finally(() => console.log("Data Sent", { data: data }))
      .catch((err) => {
        setfail(failcount + 1);
        setmodal(true);
        setpayment("error");
        console.log(err);
      });
  };

  const handleInputFocus = (e) => {
    onChangeField(e, "focus", e.target.name);
  };

  useEffect(() => {
    if (formRef.current !== null) setTouched(formRef.current.checkValidity());
  }, []);

  if (state.paymentMethod !== "credit card")
    return (
      <>
        <Redirect to="/start/thankyou" />
      </>
    );

  return (
    <>
      <Step
        heading={
          <>
            Fantastic, please input your credit card details below to pay
            online!
            <a
              href="#"
              onClick={(e) => {
                e.preventDefault();
                toggleModal();
              }}
            >
              Terms of Service
            </a>{" "}
            please select I Agree to decline please select I Decline
          </>
        }
      >
        <img
          src={BackBtn}
          className="btn-back"
          onClick={() => history.push(prevStep)}
        />
        <div id="PaymentForm" className="mt-4">
          <Cards
            cvc={state.cvv}
            expiry={state.exp}
            focused={state.focus}
            name={state.nameOnCard}
            number={state.creditCardNumber}
          />

          <form ref={formRef}>
            <MDBRow center>
              <MDBCol md={4}>
                <MDBInput
                  type="tel"
                  name="creditCardNumber"
                  outline
                  label="Card Number"
                  pattern="^[0-9]+$"
                  placeholder="Card Number"
                  value={state.creditCardNumber}
                  onChange={(event) => {
                    validate();

                    if (event.target.value.length > 20) return;
                    onChangeField(
                      event,
                      "creditCardNumber",
                      event.target.value
                    );
                    cardnumberValidate(state.creditCardNumber);
                  }}
                  onFocus={(e) => handleInputFocus(e)}
                  icon="credit-card"
                  required
                />
              </MDBCol>
            </MDBRow>
            <MDBRow center>
              <MDBCol md={2}>
                <MDBInput
                  type="tel"
                  name="exp"
                  outline
                  className="form-control"
                  label="expiry"
                  placeholder="MM/YY"
                  value={state.exp}
                  icon="calendar-alt"
                  containerClass="mt-0 "
                  required
                  onChange={(event) => {
                    validate();

                    var rgx = new RegExp("^[0-9]+$");
                    settouchexp(true);
                    var value = event.target.value;
                    value = value.slice(0, 5);
                    setcount(count + 1);
                    onChangeField(
                      event,
                      "exp",
                      _handlingCardExpiry(value, state)
                    );
                    setErrorMsg(
                      rgx.test(event.target.value.substring(0, 1)) &&
                        state.exp.length > 3
                        ? ""
                        : "Please Enter a valid expiry date"
                    );
                  }}
                  onFocus={(e) => handleInputFocus(e)}
                />
              </MDBCol>
              <MDBCol md={2}>
                <MDBInput
                  label="CVV"
                  type="tel"
                  maxLength="3"
                  containerClass="mt-0 "
                  name="cvv"
                  outline
                  value={state.cvv}
                  required
                  onFocus={(e) => handleInputFocus(e)}
                  onChange={(event) => {
                    var rgx = new RegExp("^[0-9]+$");

                    var { value, maxLength } = event.target;
                    validate();
                    value = value.slice(0, maxLength);
                    onChangeField(event, "cvv", value);
                    cvvvalidate(state.cvv);
                  }}
                  icon="lock"
                />
              </MDBCol>
            </MDBRow>
            <MDBRow center className="mt-0">
              <MDBCol md={4} className="mt-0">
                <MDBInput
                  type="text"
                  containerClass="mt-0 "
                  name="nameOnCard"
                  outline
                  required
                  label="Name on card"
                  placeholder="name"
                  icon="user"
                  required
                  value={state.nameOnCard}
                  onChange={(event) => {
                    setcount(count + 1);
                    settouchname(true);
                    validate();
                    onChangeField(event, "nameOnCard", event.target.value);
                  }}
                  onFocus={(e) => handleInputFocus(e)}
                />
              </MDBCol>
            </MDBRow>
            <MDBRow center className="mt-0">
              <MDBCol md={4} className="mt-0">
                <MDBInput
                  type="text"
                  containerClass="mt-0 "
                  name="paymentzipcode"
                  outline
                  required
                  label="Zip Code"
                  placeholder=""
                  pattern="^[0-9]+$"
                  required
                  value={state.paymentzipcode}
                  onChange={(event) => {
                    setcount(count + 1);
                    settouchname(true);
                    validate();
                    onChangeField(event, "paymentzipcode", event.target.value);
                    setErrorMsg(
                      state.paymentzipcode.length > 6
                        ? "Enter a valid zip code"
                        : ""
                    );
                  }}
                  onFocus={(e) => handleInputFocus(e)}
                />
              </MDBCol>
            </MDBRow>
          </form>
        </div>
        <MDBRow center classNam="d-flex flex-column">
          <MDBCol>
            {touched && errorMsg.length > 0 && (
              <div
                className="text-danger mb-2 "
                style={{ textAlign: "center" }}
              >
                {errorMsg}
              </div>
            )}
            <MDBRow center className="d-flex  align-items-center">
              <MDBCol md={6} lg={4} className="mt-2">
                {touched &&
                  touchcard &&
                  touchcvv &&
                  touchexp &&
                  touchname &&
                  !errorMsg && (
                    <MDBBtn
                      color="grey"
                      rounded
                      type="button"
                      className="btn-block z-depth-1a"
                      onClick={(e) => {
                        onChangeField(e, "agree", "I Decline");
                        history.push("/start/decline");
                      }}
                    >
                      I Decline
                    </MDBBtn>
                  )}
                {(!touched ||
                  !touchcard ||
                  !touchcvv ||
                  !touchexp ||
                  !touchname ||
                  errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    rounded
                    disabled
                    type="button"
                    className="btn-block z-depth-1a"
                    onClick={(e) => {
                      onChangeField(e, "agree", "I Decline");
                      history.push("/start/decline");
                    }}
                  >
                    I Decline
                  </MDBBtn>
                )}
              </MDBCol>
              <MDBCol md={6} lg={4} className="mt-2">
                {touched &&
                  touchcard &&
                  touchcvv &&
                  touchexp &&
                  touchname &&
                  !errorMsg && (
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="btn-block z-depth-1a"
                      onClick={() => submitHandler()}
                    >
                      I Agree
                    </MDBBtn>
                  )}
                {(!touched ||
                  !touchcard ||
                  !touchcvv ||
                  !touchexp ||
                  !touchname ||
                  errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="btn-block z-depth-1a"
                  >
                    I Agree
                  </MDBBtn>
                )}
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
      </Step>
      <MDBContainer>
        <MDBModal isOpen={modal} toggle={toggle}>
          <MDBModalHeader toggle={toggle}>Oops!</MDBModalHeader>
          <MDBModalBody>
            <div>
              Unfortunately, we were unable to process the credit card details
              you provided please try again{" "}
            </div>
          </MDBModalBody>
          <MDBModalFooter>
            <MDBBtn color="secondary" onClick={toggle}>
              Try Again
            </MDBBtn>
          </MDBModalFooter>
        </MDBModal>
      </MDBContainer>

      <MDBModal size="fluid" isOpen={showModal} toggle={toggleModal}>
        <MDBModalHeader toggle={toggleModal}>Terms of Service</MDBModalHeader>
        <MDBModalBody>
          <TermsOfService />
        </MDBModalBody>
      </MDBModal>
    </>
  );
}

const _handlingCardExpiry = (text, state) => {
  if (text.indexOf(".") >= 0 || text.length > 5) {
    // Since the keyboard will have a decimal and we don't want
    // to let the user use decimals, just exit if they add a decimal
    // Also, we only want 'MM/YY' so if they try to add more than
    // 5 characters, we want to exit as well
    return;
  }

  if (text.length === 2 && state.exp.length === 1) {
    // This is where the user has typed 2 numbers so far
    // We can manually add a slash onto the end
    // We check to make sure the current value was only 1 character
    // long so that if they are backspacing, we don't add on the slash again
    text += "/";
  }

  // Update the state, which in turns updates the value in the text field
  return text;
};

function validatenum(evt) {
  var theEvent = evt || window.event;

  // Handle paste
  if (theEvent.type === "paste") {
    key = evt.clipboardData.getData("text/plain");
  } else {
    // Handle key press
    var key = theEvent.keyCode || theEvent.which;
    key = String.fromCharCode(key);
  }
  var regex = /[0-9]|\./;
  if (!regex.test(key)) {
    theEvent.returnValue = false;
    if (theEvent.preventDefault) theEvent.preventDefault();
  }
}
