import React, { Component } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import axios from "axios";
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step4 from "./Step4";
import Step5 from "./Step5";
import Step6 from "./Step6";
import Step7 from "./Step7";
import Step8 from "./Step8";
import Step9 from "./Step9";
import Step10 from "./Step10";

// import AllOptions from "./AllOptions";
import ThankYou from "./ThankYou";
import Decline from "./Decline";
export default class MultiStepForm extends Component {
  state = {
    // step1
    firstName: "",
    lastName: "",

    //step 2
    company_name: "",

    //step3
    latLng: {},
    streetAddress: "",
    aptUnitNo: "",
    city: "",
    state: "",
    zipCode: "",

    // step4
    nonProfitOrganization: "",

    // step5
    typeOfOrganization: "",

    // step6
    franchiseOfCompany: "",

    // step8
    scheduleDate: "",
    scheduleTime: "",

    // step9
    paymentMethod: "",
    paymentzipcode: "",

    // step9
    phone: "",

    // step10
    email: "",

    // step10 -- [payment]
    cvv: "",
    exp: "",
    focused: "",
    nameonCard: "",
    creditCardNumber: "",

    // step12
    ownershipOfBusinessDate: "",

    // step13
    businessDescription: "",

    // step14:
    numberOfEmployees: "",

    // step15
    ein: "",

    // step15.2
    ssn: "",

    // step16
    title: "",

    // step17
    percentage: "",

    // step18
    dob: "",

    // step19
    state: "",

    //step 20
    step19_latLng: {},
    step19_streetAddress: "",
    step19_city: "",
    step19_state: "",
    step19_zipCode: "",

    // step21
    usCitizen: "",

    // step22
    convictedOrCriminalAction: "",

    // step23
    advantageOfCovid19Grant: "",

    // step24
    bankName: "",
    accountNumber: "",
    routingNumber: "",

    // step25
    agree: "",
  };
  handleChangeField = (e, name, value) => {
    this.setState(
      {
        [name]: value,
      },
      () => console.log(this.state)
    );
  };

  submitData() {
    const data = {
      firstName: this.state.firstName,
      lastName: this.state.lastName,
      latLng: this.state.latLng,
      streetAddress: this.state.streetAddress,
      aptUnitNo: this.state.aptUnitNo,
      city: this.state.city,
      state: this.state.state,
      zipCode: this.state.zipCode,
      nonProfitOrganization: this.state.nonProfitOrganization,
      typeOfOrganization: this.state.typeOfOrganization,
      franchiseOfCompany: this.state.franchiseOfCompany,
      growsRevenueFor12Months: this.state.growsRevenueFor12Months,
      expensesFor12Months: this.state.expensesFor12Months,
      phone: this.state.phone,
      email: this.state.email,
      businessEstablishmentDate: this.state.businessEstablishmentDate,
      ownershipOfBusinessDate: this.state.ownershipOfBusinessDate,
      businessDescription: this.state.businessDescription,
      numberOfEmployees: this.state.numberOfEmployees,
      ein: this.state.ein,
      ssn: this.state.ssn,
      title: this.state.title,
      percentage: this.state.percentage,
      dob: this.state.dob,
      state: this.state.state,
      address: `${this.state.step19_streetAddress}, ${this.state.step19_city}, ${this.state.step19_state} ${this.state.step19_zipCode}`,
      usCitizen: this.state.usCitizen,
      convictedOrCriminalAction: this.state.convictedOrCriminalAction,
      advantageOfCovid19Grant: this.state.advantageOfCovid19Grant,
      company_name: this.state.company_name,
      bankName: this.state.bankName,
      accountNumber: this.state.accountNumber,
      routingNumber: this.state.routingNumber,
      agree: this.state.agree,
    };
    axios
      .post("https://hooks.zapier.com/hooks/catch/1712527/o5y1jta/", data)
      .then((response) => console.log(response))
      .finally(() => console.log("Data Sent", { data: data }));
  }

  renderStep = (props) => {
    const { history } = props;
    const { path, params } = props.match;
    if (params.step === "thankyou") {
      this.submitData();
      return <ThankYou state={this.state} />;
    } else if (params.step === "decline") {
      return <Decline />;
    }
    const step = parseInt(params.step);
    const currentPath = `/${path.split("/")[1]}`;
    switch (step) {
      case 1:
        return (
          <Step1
            history={history}
            nextStep={`${currentPath}/2`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 2:
        return (
          <Step2
            history={history}
            prevStep={`${currentPath}/1`}
            nextStep={`${currentPath}/3`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 3:
        console.log("step 3");

        return (
          <Step3
            history={history}
            prevStep={`${currentPath}/2`}
            nextStep={`${currentPath}/4`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 4:
        console.log("step 4");
        return (
          <Step4
            history={history}
            prevStep={`${currentPath}/3`}
            nextStep={`${currentPath}/5`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 5:
        return (
          <Step5
            history={history}
            prevStep={`${currentPath}/4`}
            nextStep={`${currentPath}/6`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 6:
        return (
          <Step6
            history={history}
            prevStep={`${currentPath}/5`}
            nextStep={`${currentPath}/7`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 7:
        return (
          <Step7
            history={history}
            prevStep={`${currentPath}/6`}
            nextStep={`${currentPath}/8`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 8:
        return (
          <Step8
            history={history}
            prevStep={`${currentPath}/7`}
            nextStep={`${currentPath}/9`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 9:
        return (
          <Step9
            history={history}
            prevStep={`${currentPath}/8`}
            nextStep={`${currentPath}/10`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      case 10:
        return (
          <Step10
            history={history}
            prevStep={`${currentPath}/9`}
            nextStep={`${currentPath}/thankyou`}
            state={this.state}
            onChangeField={this.handleChangeField}
          />
        );
      default:
        return <div>invalid step</div>;
    }
  };
  render() {
    const { path } = this.props.match;
    return (
      <Switch>
        <Route
          path="/start"
          exact
          render={() => <Redirect to={`${path}/1`} />}
        />
        <Route
          path={`${path}/:step`}
          render={(props) => (
            <>
              {this.renderStep(props)}
              <small
                style={{
                  position: "relativr",
                  display: "flex",
                  justifyContent: "center",
                  width: "100%",
                  bottom: "-49vh",
                  position: "relative",
                  textAlign: "center",
                  padding: 10,
                }}
              ></small>
            </>
          )}
        />
      </Switch>
    );
  }
}
