import React from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step5(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;

  return (
    <Step heading={`What's your Sex?`}>
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "sex", "male");
                    history.push(nextStep);
                  }}
                >
                  Male
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2"></MDBCol>
              <MDBCol md={6} lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "sex", "female");
                    history.push(nextStep);
                  }}
                >
                  Female
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
