import React from "react";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";

export default function Step4(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;

  return (
    <Step
      heading={`Wonderful, I have you scheduled for ${state.scheduleDate} at ${state.scheduleTime} at our 11121 Hickman Road, Urbandale, IA 50322 Location! How would you like to pay your 80 dollar test fee?`}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <MDBRow center>
          <MDBCol sm={10} md={8}>
            <div className="my-4" />
            <MDBRow center>
              <MDBCol lg={3}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "paymentMethod", "cash");
                    history.push("/start/thankyou");
                  }}
                >
                  Cash
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={4}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(e, "paymentMethod", "credit card");
                    history.push(nextStep);
                  }}
                >
                  Credit Card
                </MDBBtn>
              </MDBCol>
              <MDBCol className="d-block d-lg-none my-2" />
              <MDBCol lg={5}>
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="btn-block z-depth-1a"
                  onClick={(e) => {
                    onChangeField(
                      e,
                      "paymentMethod",
                      "my employer is a corporate partner"
                    );
                    history.push("/start/thankyou");
                  }}
                >
                  My Employer is a Corporate Partner
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Step>
  );
}
